import React from 'react';
import Variablesairplane from '../../assets/images/variablesairplanenormal.png';
import Submissionairplane from '../../assets/images/submissionairplanenormal.png';

function  Dataset2(props) {
    
    return(
        <div className="container">
            <h4>Dataset : </h4>
            <div className="paragraph">Dataset includes parameters recorded during incident.
                <br/>Given below is screenshot for variables used in dataset<br/>
                </div>
            <div className="outputdataimage" style={{overflowX:'auto'}}>
                
            <img src={Variablesairplane} alt="variables for aeroplane" />
            </div>
            <div >
                <h4>Databook : </h4>
            </div>
            <ul>
            <li >
                <h6>Accident_ID - </h6>
                <p className="paragraph">
                    Unique ID assigned to each accident (row)
                </p>
            </li>
            <li >
                <h6>Accident_Type_Code - </h6>
                <p className="paragraph">
                    Defines type of accident using value from 1-7
                </p>
            </li>
            <li >
                <h6>Cabin_Temperature - </h6>
                <p className="paragraph">
                    Last recorded temperature before the incident.
                    <br/>Measured in degrees Farenheit
                </p>
            </li>
            <li >
                <h6>Turbulence_In_gforces - </h6>
                <p className="paragraph">
                    Recorded/estimated turbulance experienced during
                    <br/>accident(measured in gforces).
                </p>
            </li>
            <li >
                <h6>Control_Metric -</h6>
                <p className="paragraph">
                    Estimation of how much control the pilot had
                    <br/>during the incident given factors at play.
                </p>
            </li>
            <li >
                <h6>Total_Safety_Complaints -</h6>
                <p className="paragraph">
                    Number of complaints from mechanics prior to the accident. 
                </p>
            </li>
            <li >
                <h6>Days_Since_Inspection -</h6>
                <p className="paragraph">
                    How long plane went without inspection before the incident.
                </p>
            </li>
            <li >
                <h6>Violations -</h6>
                <p className="paragraph">
                    Number of violation that aircraft recieved during inspections.
                </p>
            </li>
            <li >
                <h6>Severity -</h6>
                <p className="paragraph">
                    Description of severity of crash.
                    <br/>This is what we are predicting on test-dataset after making model using Train-dataset.
                    <br/>It has 4 categories - Minor_Damage_And_Injuries , Significant_Damage_And_Fatalities ,
                    <br/>Significant_Damage_And_Serious_Injuries , Highly_Fatal_And_Damaging
                </p>
            </li>
            </ul>
            <div>
            <h4>Submission:</h4>
            <p className="paragraph">
                Submission dataset includes results  for test dataset when test dataset
                was fitted in model created by training dataset.
                It includes 2 columnd for AirplaneId and Severity. <br/>
            </p>
            </div>
            <div className="outputdataimage" style={{overflowX:'auto'}}>
                
            <img src={Submissionairplane} alt="submission for aeroplane" />
            </div>
        </div>
        
    );
}

export default Dataset2;