import React, { Component } from 'react';
import {Jumbotron} from 'reactstrap';
import {Link} from 'react-router-dom'

class Foot extends Component{

    render(){

        return (
            <div className="container">
               <Jumbotron className="paragraph jumbolink">
                   <div className="row ">
                       <span className="m-auto">
                       <Link className="links2 " to="/home" style={{textDecoration:'none'}}>
                       <span className="fa fa-home fa-lg"></span>Home
                       </Link>
                       <hr className="horizontal"/>
                       </span>
                       <span className="m-auto">
                       <Link className="links2" to="/aeroplane" style={{textDecoration:'none'}}>
                       <span className="fa fa-plane fa-lg"></span>Aeroplane
                       </Link>
                       <hr className="horizontal"/>
                       </span>
                       <span className="m-auto">
                       <Link className="links2" to="/titanic" style={{textDecoration:'none'}}>
                       <span className="fa fa-ship fa-lg"></span>Titanic
                       </Link>
                       <hr className="horizontal"/>
                       </span>
                       <span className="m-auto">
                       <Link className="links2" to="/feedback" style={{textDecoration:'none'}}>
                       <span className="fa fa-list fa-lg"></span>Feedback
                       </Link>
                       <hr className="horizontal"/>
                       </span>
                   </div>
                   <div className="row">
                        <h5 className="mr-auto ml-auto"><strong>Datasets And Python Code Will Be Given Seperately</strong></h5>
                   </div>
               </Jumbotron>
           </div>

        );
        
    }
}

export default Foot;