import React, { Component } from 'react';
import Dataset1 from './titanic/Dataset1Component';
import Intro1 from './titanic/Intro1Component';
import Python1 from './titanic/Python1Component';
import Header1 from './titanic/Header1Component';
import {Jumbotron} from 'reactstrap';
import {Switch, Route, Redirect} from 'react-router-dom';


class Titanic extends Component{

    render(){
        
        return (
           <div className="container">
               <div className="row">
                <Jumbotron className="jumbolink col-2">
                    <Header1 />
                </Jumbotron>
                <Jumbotron className="jumbo col-10">
                    <Switch>
                        <Route path="/titanic/intro1" component={Intro1} />
                        <Route path="/titanic/python1" component={Python1} />
                        <Route path="/titanic/dataset1" component={Dataset1} />
                        <Redirect to="/titanic/intro1" />
                    </Switch>
                </Jumbotron>
               </div>
            </div>
        );
   
    }
}

export default Titanic;