import React, { Component } from 'react';
import Dataset2 from './aeroplane/Dataset2Component';
import Intro2 from './aeroplane/Intro2Component';
import Python2 from './aeroplane/Python2Component';
import Header2 from './aeroplane/Header2Component';
import {Jumbotron} from 'reactstrap';
import {Switch, Route, Redirect} from 'react-router-dom';


class Aeroplane extends Component{

    render(){

        const Python2page =() =>{
            return(
                <Python2 python2data={this.props.python2data} />
            );
        }
        
        return (
           <div className="container">
               <div className="row">
                <Jumbotron className="jumbolink col-2">
                    <Header2 />
                </Jumbotron>
                <Jumbotron className="jumbo col-10">
                    <Switch>
                        <Route path="/aeroplane/intro2" component={Intro2} />
                        <Route path="/aeroplane/python2" component={Python2page} />
                        <Route path="/aeroplane/dataset2" component={Dataset2} />
                        <Redirect to="/aeroplane/intro2" />
                    </Switch>
                </Jumbotron>
               </div>
            </div>
        );
   
    }
}

export default Aeroplane;