import React from 'react';

function  Intro1(props) {
    
    return(
        <div className="container">
            <div className="row">
                <h4>Titanic Survivors</h4>
                <p className="paragraph">
                    Titanic was indeed a heartwrenching accident .Some people survived and some not. 
                    Information regarding passengers is given to us but some information is missing 
                    lets build a model to predict those missed values to know if that person survived or 
                     not .
                </p>
            </div>
            <div className="row ">
                <h4>Dataset for Titanic accident is given :</h4> 
                We will be using training dataset to make a model using XGBoost Algorithm
                <br/>  and then predict whether a passenger survived or not test data .
            </div>
            <br/>
            <div className="row">
                <h4>Data Handling:</h4>
                <ol>
                <li className="paragraph">Made new variables to extract information from present variable.
                <br/>Example: made 'Title' variable from 'Name' inorder to extract designation.</li>
                <li className="paragraph">Mapped categorical variables to number, made
                <br/>dictionary and assigned necessary values to categories.  
                <br/>Example: mapped 'Sex' variable's category as 'male'-0 and 'female'-1.</li>
                <li className="paragraph">Dropped 'Name' variable from dataset as we have stored necessary 
                <br/>information from 'Name' into 'Title'.</li>
                <li className="paragraph">Filled the missing values in 'Age' by first grouping data according to
                <br/>'Title' categories ,then finding Median for 'Age' for each group and Filled
                <br/>missing Ages for each group with thier respective Median.</li>
                <li className="paragraph">Filled the misssing values in embarked.
                <br/>To do this we plotted a bar chart to depict travelling location of 
                <br/>people according to thier class. It can be seen that irrespective of class
                <br/>majority of people aur travelling to Southampton and so fixed S for misssing emarked.</li>
                <li className="paragraph">Now mapped 'Embarked' categories to values.</li>
                <li className="paragraph">Filled the missing values in 'Fares' by first grouping data according to
                <br/>Income class ,then finding Median for 'Fares' for each class and Filled
                <br/>missing Fares with median fares for respective groups.</li>
                <li className="paragraph">8-Made categories for Fare variable .</li>
                <li className="paragraph" >Filled in values for 'Cabin' using similar approach.</li>
                <li className="paragraph">Made a new variable 'FamilySize' which includes total family size.
                <br/>After that mapped values for 'FamilySize' using dictionary.</li>
                <li className="paragraph">Dropped 'Parch', 'SibSp' and 'Ticket' variables as they are not of
                <br/>any use in building model.</li>
                </ol>
            </div>
            <div className="row" >
                <h4>Final Step:</h4>
                <p className="paragraph">
                    Finally we made model using XGBoost Algo by specifying
                    necessary things inside its parameters.
                    and did cross validation using k-fold cross validation between training data and 
                    target which stored value of 'Survived' variable in training data .
                    Now we fitted our model and predicted using test-dataset the value of 
                    Survived for each passenger in test-dataset and stored information in Submission.csv.  
                </p>
            </div>
        </div>
    );
}

export default Intro1;