import React from 'react';
import Variablestitanic from '../../assets/images/variablestitanicnormal.png';
import Submissiontitanic from '../../assets/images/submissiontitanicnormal.png';

function  Dataset1(props) {
    
    return(
        <div className="container">
            <div>
                <h4>Given below is screenshot for variables used in dataset</h4><br/>
            </div>
        <div className="outputdataimage" style={{overflowX:'auto'}}>
            <img src={Variablestitanic} alt="variables for titanic" />
        </div>
        <br/>
        <div >
            <h4>Databook : </h4>
        </div>
        <ul>
        <li>
            <h6>PassengerId - </h6>
            <p className="paragraph">
                Id associated uniquely to each passenger 
            </p>
        </li>
        <li>
            <h6>Survived - </h6>
            <p className="paragraph">
                It is the response variable having two values 0 for survived and 1 for not survived.
                <br/>this is the variable for which we will make model 
                <br/>as finally a person survived or not is the result
                <br/>not provided in test dataset we will be predicting it
            </p>
        </li>
        <li>
            <h6>Pclass - </h6>
            <p className="paragraph">
                in which class had passenger reserved his seat
                <br/>1-for upper class, 2-for middle class, 3-for lower class
            </p>
        </li>
        <li>
            <h6>Name - </h6>
            <p className="paragraph">Name of passenger along with the designation he gave
                <br/>designation includes following
                <br/>Mr, Miss, Mrs, Master, Dr, Rev, Col, Major, Mlle, Countess
                <br/> , Ms, Lady, Jonkheer, Don, Dona, Mme, Capt, Sir 
            </p>
        </li>
        <li>
            <h6>Sex - </h6>
            <p className="paragraph">
                states whether person is male or female
            </p>
        </li>
        <li>
            <h6>Age - </h6>
            <p className="paragraph">
                age of the person
            </p>
        </li>
        <li>
            <h6>Fare - </h6>
            <p className="paragraph">
                amount paid for ticket in dollars
            </p>
        </li>
        <li>
            <h6>Ticket - </h6>
            <p className="paragraph">
                Contains ticket id
            </p>
        </li>
        <li>
            <h6>SibSp - </h6>
            <p className="paragraph">
                Number of siblings/spouses aboard
                <br/>Siblings-Brother,Sister,Stepbrother,Stepsister
                <br/>Spouse-Husband,Wife
            </p>
        </li>
        <li>
            <h6>Parch - </h6>
            <p className="paragraph">
                Number of parents/children aboard
                <br/>Parent- Mother,Father
                <br/>Child- Daughter, Son ,Stepdaughter, Stepson
                <br/>Parch is zero for those who travelled with guardians
            </p>
        </li>
        <li>
            <h6>Embark - </h6>
            <p className="paragraph">
                port from where passenger embarked his journey
                <br/> S-Southampton , Q-Quenston and C-Cherbourg
            </p>
        </li>
        <li>
            <h6>Cabin - </h6>
            <p className="paragraph">
                Refers to cabin number 
            </p>
        </li>
        </ul>
        <div >
            <h4>Submission : </h4>
            <p className="paragraph">
                Submission dataset includes results  for test dataset when test dataset
                was fitted in model created by training dataset.
                It includes 2 for PassengerId and whether he survive or not. <br/>
            </p>
        </div>
        <div className="outputdataimage" style={{overflowX:'auto'}}>
       
        <img src={Submissiontitanic} alt="submission for titanic" />
        </div>
    </div>
    );
}

export default Dataset1;