import React from 'react';

function  Intro2(props) {
    
    return(
        <div className="container">
            <div className="row">
                <h4>Airplane Accidents</h4>
                <p className="paragraph">
                    We will build Machine Learning model to anticipate and classify the severity of an 
                    airplane accident based on past incidents. With this, all airlines, even the entire aviation 
                    industry, can predict the severity of airplane accidents caused due to various factors and, correspondingly,
                     have a plan of action to minimize the risk associated with them.
                </p>
            </div>
            <div className="row">
                <h4>Training Dataset for accidents is given :</h4>
                  We will be using training dataset to make a model
                <br/>using XGBoost Algorithm and Random Forest and then predict results for Test data .
                <br/>Importance -We can add values for other airplane in testing data set and predict whether accident.
                 
            </div>
            <br/>
            <div className="row">
                <h4>What We Did In Python Code</h4>
                <ol>
                <li className="paragraph"> We defined numeric value for 4 different categories of severity.</li>
                <li className="paragraph"> Represented explanatory variables using x for x_train , x_test.</li>
                <li className="paragraph"> Represented response variable i.e.Severity using y.</li>
                <li className="paragraph"> Splitted the given training data-set to train and test for x and y.</li>
                <li className="paragraph"> Made two models using Random Forest and XGBoost Algorithm using soft voting and hard voting.</li>
                <li className="paragraph"> Now fitted these models on x_train,y_train and checked score for them.</li>
                <li className="paragraph"> Now predicted outcome for each model using x_test and got accuracy-score by comparing 
                <br/>each prediction with y_test which was original answer.</li>
                <li className="paragraph"> We see that model made using soft voting have a high accuracy score .</li>
                <li className="paragraph"> We select this as our model and use test-dataset given originally and predicted
                <br/>severity for it and stored in a submission4.csv file.</li>
                </ol>
            </div>
        </div>
    );
}

export default Intro2;