import React, { Component } from 'react';
import Head from './HeadComponent';
import Foot from './FootComponent';
import Home from './HomeComponent'
import Aeroplane from './AeroplaneComponent'
import Titanic from './TitanicComponent'
import Feedback from './FeedbackComponent'
import {Switch, Route, Redirect, withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {addFeedback} from '../redux/ActionCreators';

const mapStateToProps = state => {
    return {
        feedbackdata: state.feedbackdata,
        python2data: state.python2data
    }
}

const mapDispatchToProps = (dispatch) => ({
    addFeedback: (name , message) => dispatch(addFeedback(name , message))
});

class Main extends Component{
    
    render() {

        const Feedbackpage = () =>{
            return(
                <Feedback feedbackdata={this.props.feedbackdata} 
                addFeedback={this.props.addFeedback} />
            );
        }

        const Aeroplanepage = () =>{
            return(
                <Aeroplane python2data={this.props.python2data} />
            );
        }

        return (
            <div className="container">
                <Head />
                <Switch>
                    <Route exact path="/home" component={Home} />
                    <Route path="/aeroplane" component={Aeroplanepage} />
                    <Route path="/titanic" component={Titanic} />
                    <Route exact path="/feedback" component={Feedbackpage} />
                    <Redirect to="/home" />
                </Switch>
                <Foot />
            </div>

        );
        
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));