import React, { Component } from 'react';
import {Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem} from 'reactstrap';
import {NavLink} from 'react-router-dom'

class Head extends Component{

    constructor(props){
        super(props);
        this.state={
            isNavOpen: false
        };
        this.toggleNav=this.toggleNav.bind(this);
    }

    toggleNav() {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }

    render(){

        return (
            <div className="container">
            <Navbar dark expand="md">
                <div>
                    <NavbarToggler onClick={this.toggleNav} />
                    <NavbarBrand className=" navbarbrand mr-auto">
                        Summer Internship Project
                    </NavbarBrand>
                    <Collapse isOpen={this.state.isNavOpen} navbar>
                        <Nav navbar>
                            <NavItem>
                                <NavLink className="nav-link" to="/home" onClick={this.toggleNav}>
                                    <span className="fa fa-home fa-lg"></span>Home
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/aeroplane" onClick={this.toggleNav}>
                                    <span className="fa fa-plane fa-lg"></span>Aeroplane
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/titanic" onClick={this.toggleNav}>
                                    <span className="fa fa-ship fa-lg"></span>Titanic
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/feedback" onClick={this.toggleNav}>
                                    <span className="fa fa-list fa-lg"></span>Feedback
                                </NavLink>
                            </NavItem>
                        </Nav>
                    </Collapse>
                    
                </div>
            </Navbar>
            </div>

        );
        
    }
}

export default Head;