import React from 'react';
import {Link} from 'react-router-dom'

function  Header1(props) {
    
    return(
        <div >
            <div>
                <Link className="links" to="/titanic/intro1" style={{textDecoration:'none'}}>
                    Introduction
                </Link>
                <hr />
            </div>
            <div >
                <Link className="links" to="/titanic/python1" style={{textDecoration:'none'}}>
                    Python Code
                </Link>
                <hr />
            </div>
            <div >
                <Link className="links"to="/titanic/dataset1" style={{textDecoration:'none'}}>
                    Databook
                </Link>
                <hr />
            </div>
        </div>
    );
}

export default Header1;