import React, { Component } from 'react';
import {Jumbotron,} from 'reactstrap';
import {Link} from 'react-router-dom';

class Home extends Component{

    render(){

        return (
            <div className="container">
                <Jumbotron className="jumbo">
                    <Link to="/aeroplane" style={{textDecoration:'none'}} className="links3" >
                        <h2>Airplane Accident Severity Prediction</h2>
                        <p>
                        Flying has been the go-to mode of travel for years now; it is time-saving,
                        <br/>  affordable, and extremely convenient . According to the FAA, 2,781,971 passengers fly every day 
                        <br/>in the US, as in June 2019. Passengers reckon that flying is very safe, considering strict inspections 
                        <br/>are conducted and security measures are taken to avoid and/or mitigate any mishappenings.
                        <br/>However, there remain a few chances of unfortunate incidents.</p>
                    </Link>
                </Jumbotron>
                <Jumbotron className="jumbo">
                    <Link to="/titanic" style={{textDecoration:'none'}} className="links3">
                        <h2>Titanic Accident Survival Prediction</h2>
                        <p>
                        Titanic was indeed a heartwrenching accident .Some people survived and some not.
                        <br/> Information regarding passengers is given to us but some information is missing 
                        <br/>lets build a model to predict those missed values to know if that person survived or 
                        <br/>not .
                        </p>
                    </Link>
                </Jumbotron>
               
           </div>

        );
        
    }
}

export default Home;