import React, { Component } from 'react';
import {Button, Col, Row, Label,Modal, ModalHeader, ModalBody} from 'reactstrap';
import {Control, LocalForm, Errors } from 'react-redux-form';
import Jumbotron from 'reactstrap/lib/Jumbotron';


const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);

class Feedbackform extends Component{

    constructor(props){
        super(props);
        this.state={
            isModalOpen: false
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
    }

    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }
    handleSubmit(values) {
        this.toggleModal();
        this.props.addFeedback(values.name, values.message);
    }

    render(){

        return (
           <div>
               <Button outline onClick={this.toggleModal}>
                   <span>Feedback</span> 
               </Button>
               <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                   <ModalHeader toggle={this.toggleModal}><span >Give your feedback </span></ModalHeader>
                   <ModalBody>
                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                            <Row className="form-group">
                                <Label htmlFor=".name" md={2}>Name</Label>
                                <Col md={10}>
                                    <Control.text model=".name" id="name" name="name" placeholder="Your Name"
                                    className="form-control" 
                                    validators={{
                                          required, minLength: minLength(3), maxLength: maxLength(15)       
                                    }}
                                    />
                                    <Errors
                                        className="text-danger"
                                        model=".name"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 characters',
                                            maxLength: 'Must be 15 characters or less'
                                        }}
                                    />
                                </Col> 
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor=".message" md={2}> Your Feedback</Label>
                                <Col md={10}>
                                    <Control.textarea model=".message" id="message" name="message" placeholder="type your text..."
                                    rows="12" className="form-control" 
                                    validators={{
                                        required, minLength: minLength(6), maxLength: maxLength(100)       
                                  }}
                                  />
                                  <Errors
                                        className="text-danger"
                                        model=".message"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 5 characters',
                                            maxLength: 'Must be 100 characters or less'
                                        }}
                                    />
                                </Col> 
                            </Row>
                            <Row className="form-group">
                                <Col md={{size: 6, offset: 2}}>
                                    <div className="form-check">
                                        <Label check>
                                            <Control.checkbox model=".agree" name="agree" 
                                            className="form-check-input" />{' '}
                                            <strong>are you sure?</strong>
                                        </Label>
                                    </div>
                                </Col> 
                            </Row>
                            <Row className="form-group">
                                <Col md={{size:10, offset:2}}>
                                    <Button type="submit" color="primary">
                                        Send Feedback
                                    </Button>
                                </Col> 
                            </Row>
                       </LocalForm>
                   </ModalBody>
               </Modal>
           </div>

        );
    
    }
}

const Feedbackbody = ({feedbackdata , addFeedback}) => {
    if(feedbackdata!=null){
        return(
            <div className="col-12 col-md-5 m-1">
                <br/>
                <ul className="list-unstyled">
                    {feedbackdata.map((feedback) =>{
                        return(
                            <li key={feedback.id}>
                            <span>{feedback.id +1}:&nbsp;&nbsp;{feedback.message}</span>
                            <p>--
                            {feedback.name}</p>   
                            </li>
                        );
                    })}
                </ul>
                <Feedbackform addFeedback={addFeedback}/>
            </div>
        );
        }
    else{
        return(
            <div></div>
        );
    }             
    }

const Feedback = (props) =>{
    return(
        <div className="container">
            <Jumbotron className="jumbo">
               <div className="row">
                   <div className="col-12">
                       <h4>Why XGBoost?</h4>
                       <p className="paragraph1">XGBoost is a decision-tree-based ensemble Machine Learning algorithm that uses a gradient boosting framework. In prediction problems involving unstructured data (images, text, etc.) artificial neural networks tend to outperform all other algorithms or frameworks. However, when it comes to small-to-medium structured/tabular data, decision tree based algorithms are considered best-in-class right now.</p>
                       <br/><p><h6>Tianqi Chen( co-creator):</h6></p>
                       <p className="paragraph1">"innovative sysytem features and algorithmic optimizations in 
                       <br/>XGBoost have rendered it 10 times faster than most sought after machine learning solutions."</p>
                       <h4>Give your essential feedback</h4>
                   </div>
               </div>
               <div className="row ">
                   <Feedbackbody feedbackdata={props.feedbackdata}
                   addFeedback={props.addFeedback}/>
               </div>
            </Jumbotron>
        </div>
    );
}

export default Feedback;







